.buttonGet {
  background-color: #5cdb95;
  border: none;
  border-radius: 5px;
  color: #fff;
  text-shadow: 0 0 2px #05386b;
  padding: 5px 30px;
  align-self: flex-end;
  cursor: pointer;
  font-size: 1em;
  font-weight: 600;
}
.wrap {
  display: grid;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  margin: 3rem 0;
}
.wrapUser {
  display: grid;
  justify-items: center;
}
.avatar {
  width: 100px;
}
.wrapButton {
  margin-top: 10px;
}
.wrapButton Button {
  background-color: #5cdb95;
  border: none;
  border-radius: 5px;
  color: #fff;
  text-shadow: 0 0 2px #05386b;
  padding: 5px;
  width: 100px;
  align-self: flex-end;
  cursor: pointer;
  font-size: 0.9em;
  font-weight: 600;
}
.wrapDescription {
  display: grid;
  grid-template-columns: 8fr 2fr;
  border: 2px solid #5cdb95;
  border-radius: 20px;
  padding: 20px;
  margin-right: 40px;
}
.fullName {
  font-size: 1.2em;
  font-weight: 600;
}
.statusUser {
  font-size: 1.1rem;
  font-weight: 500;
  align-self: end;
  word-break: break-all;
  padding-right: 1.5em;
}
.alignment {
  display: grid;
}
.country {
  font-weight: 600;
}
.city {
  font-size: 1em;
}