  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
  border-radius: 4px;
}
.ProfileInfo_slider__27asz:before {
  position: absolute;
  content: "";
  height: 14px;
  width: 14px;
  left: 5px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
}
.ProfileInfo_switchInput__3QRJp:checked + .ProfileInfo_slider__27asz {
  background-color: #5cdb95;
}
.ProfileInfo_switchInput__3QRJp:focus + .ProfileInfo_slider__27asz {
  box-shadow: 0 0 1px #5cdb95;
}
.ProfileInfo_switchInput__3QRJp:checked + .ProfileInfo_slider__27asz:before {
  -webkit-transform: translateX(18px);
  transform: translateX(18px);
}

